<template>
  <div class="fixed z-10 inset-0 overflow-y-auto" aria-labelledby="modal-title" role="dialog" aria-modal="true">
    <div class="flex justify-center min-h-screen sm:pt-6 sm:px-4 sm:pb-20 text-center sm:block sm:p-0">
      <div @click="$emit('closeModal')" class="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity" aria-hidden="true"></div>

      <!-- contents -->
      <div @click.stop class="inline-block bg-white rounded-lg text-left overflow-hidden shadow-xl transform transition-all sm:my-8 sm:align-middle sm:max-w-lg w-full">
        <div class=" flex justify-between items-center p-2 border-b border-default">
          <button @click="$emit('closeModal')" class="fas fa-times text-primary text-lg p-2 w-10 h-10 hover-bg-blue-50 rounded-full"></button>
          <div class="text-right sm:hidden mr-2">
            <button v-if="!tweetBody.length" class="bg-light text-sm font-bold text-white px-4 py-1 rounded-full">트윗</button>
            <button v-else @click="onAddTweet" class="bg-primary hover:bg-dark text-sm font-bold text-white px-4 py-1 rounded-full">트윗</button>
          </div>
        </div>
        <div>
          <!-- original tweet -->
          <div class="flex px-4 pt-4 pb-3">
            <div class="felx flex-">
            <img src="https://picsum.photos/100" class="w-10 h-10 mr-2 rounded-full hover:opacity-70 cursor-pointer" alt="">
            <div class="ml-5 w-0.5 h-full bg-gray-300 mt-2 -mb-1"></div>
            </div>
            <div class="flex-1 ml-2">
              <div class="flex space-x-2">
                <span class="font-bold text-sm">이름</span>
                <span class="text-light text-sm">@아이디</span>
                <span class="text-light text-sm">1시간</span>
              </div>
              <div class="text-sm">내용내용내용내용내용내용내용내용내용내용내용내용내용내용내용내용내용내용내용내용</div>
              <div>
                <span class="text-primary text-sm">@dkdlel</span>
                <span class="text-gray text-sm">누구님에게 보내는 답글</span>
              </div>
            </div>
          </div>
          <div class="flex px-4 pb-4">
            <!-- <img :src="currentUser.profile_image_url" class="w-10 h-10 mr-2 rounded-full hover:opacity-70 cursor-pointer" alt=""> -->
            <img :src="currentUser.profile_image_url" class="w-10 h-10 mr-2 rounded-full hover:opacity-70 cursor-pointer" alt="">
            <div class="flex flex-1 flex-col">
              <textarea v-model="tweetBody" rows="5" class="w-full text-lg font-bold focus:outline-none resize-none" placeholder="내 답글을 트위합니다"></textarea>
              <div class="text-right hidden sm:block">
                <button v-if="!tweetBody.length" class="bg-light text-sm font-bold text-white px-4 py-1 rounded-full">답글</button>
                <button v-else @click="onAddTweet" class="bg-primary hover:bg-dark text-sm font-bold text-white px-4 py-1 rounded-full">답글</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, computed } from "vue";
import addTweet from "/src/untils/addTweet.js";
import store from "/src/store";

export default {
  components: ["TweetBtn"],
  setup(props, {emit}) {
    const tweetBody = ref("")
    const currentUser = computed(() => store.state.user)

    const onAddTweet = async () => {
      try {
        await addTweet(tweetBody.value, currentUser.value)
        tweetBody.value = ''
        emit('close-modal')
      } catch (e) {
        console.log('on add tweet wrror on homepage: ', e)
      }
    }

    return { tweetBody, currentUser, onAddTweet };
  },
};
</script>

